.App {
  text-align: center;
}

.App-header {
  background-image: url("https://batamtoday.com/media/news/ilustrasi-bioskop-kosong1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  height: 100%;
  color: white;
}
h1 {
  color: rgb(95, 85, 85);
}

.App-link {
  color: #61dafb;
}

.Movie-container {
  width: 100%;
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.Movie-wrapper {
  width: 500px;
  background-color: antiquewhite;
  color: black;
}

.Movie-search {
  height:48px;
  font-size: 24px;
  margin-bottom: 32px;
  padding: 16px;
  font-weight: bold;
  width: 475px;
}

.Movie-title {
  height: 60px;
  margin-top: 26px;
  font-weight: bold;
}

.Movie-date {
  font-style: inherit;
  color: #565758;
  margin-top: 24px;
  text-decoration: solid;
}

.Movie-rate {
  color: red;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 1.3em;
  text-decoration: underline;
}